export const fetchjobList = async () => {
  //const jobListUrl = `${baseUrl}/user/getJobs?${params}`
  const jobListUrl = 'http://localhost:8082/user/getJobs/'+userid
  try {
    const response = await axios.get(jobListUrl)
    console.log(response)
    return response
  } catch (e) {
    throw new Error(e)
  }

  

}
